export const randomSelect = (
  min: number,
  max: number,
  take: number,
  exclude?: Set<number>
) => {
  const nums = new Set<number>();
  while (nums.size !== take) {
    const rand = Math.floor(Math.random() * (max - min + 1)) + min;
    if (!exclude || !exclude.has(rand)) nums.add(rand);
  }
  return [...nums];
};

export const randomNumber = (min: number, max: number) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

export enum Difficulty {
  easy,
  medium,
  normal,
  hard,
  hardPlus,
  impossible,
  impossiblePlus,
}

export const difficultyText: Record<Difficulty, string> = {
  [Difficulty.easy]: "",
  [Difficulty.medium]: "",
  [Difficulty.normal]: "",
  [Difficulty.hard]: "",
  [Difficulty.hardPlus]: "",
  [Difficulty.impossible]: "",
  [Difficulty.impossiblePlus]: "",
};

export const difficultyButtonText: Record<Difficulty, string> = {
  ...difficultyText,
  [Difficulty.easy]: "",
};

export interface IMatch {
  map: number;
  mutators: Array<number>;
  commanders: Array<number>;
  difficulty: Difficulty;
  win: boolean;
  startTime: number;
  recordTime: number;
}

export interface IMatchHistory {
  single: Array<IMatch>;
  multiple: Array<IMatch>;
}
